import './ChartComponent.scss';
import 'chart.js/auto';
import { Chart } from 'react-chartjs-2';
import CircularProgress from '@mui/material/CircularProgress';

const ChartComponent = ({
  chartData, 
  periodValue
}) => {

    if (!chartData) {
        return <CircularProgress  />
    }

    let labelsArray = Object.keys(chartData);
    let currencyArray = [periodValue];
    // let valuesArray = Object.entries(chartData);

    for (let i = 0; i < labelsArray.length; i++) {
        labelsArray[i] = labelsArray[i].slice(5);
    }
    
    for (let i = 0; i < periodValue; i++) {
        currencyArray[i] = Object.entries(chartData)[i][1].RUB;
    }

    const data = {
        labels: labelsArray,
        datasets: [{      
            data: currencyArray,
            backgroundColor: [
                'rgb(255, 255, 255)',
            ],
            borderColor: [
                'black'
            ],
            
            borderWidth: 1,
        }],
      };

    const options = {
      plugins: {
        legend: {
          display: false
        }
      },
        scales: {
          x: {
            grid: {
              display: false
            }
          },
          y: {
            grid: {
              display: false
            }
          }
        },
      }

    return (
        <div>
            <Chart type='line' data={data} datasetIdKey='id' options={options}/>
        </div>
    )
}

export default ChartComponent;